<template>
  <div>
    <div class="headerZINDEX">
      <mu-appbar style="width: 100%;" :color="$store.state.color">
        <div slot="left" color="#3051f1">
          <a @click="()=>{
              $router.replace('/DayControl')
            }">
            <mu-button icon slot="left" class="icon" size="20">
              <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
            </mu-button>
          </a>
        </div>
        <div style="width:100%;text-align:center;">
          <span style="font-size:18px;color:white;">事件管理</span>
        </div>
        <div slot="right" class="icon" size="20">
          <a>
            <mu-button icon slot="left" class="icon" size="20" @click="imgShowClose">
              <img src="static/img/query.png" />
            </mu-button>
          </a>
        </div>
      </mu-appbar>
      <!-- <div class="imgPosition" @click="imgShowClose" v-show="!open">
        <img src="static/img/bottomTo.png" alt srcset />
      </div> -->
    </div>
    <div class="containerListContent" :style="back">
      <div
        class="contentList"
        v-for="(item,index) in list"
        :key="index"
        @click="clickListBtn(item.name)"
      >
        <div class="listNameTitle">
          <span style="color:#7a7a7a">事件编号:</span>
          <span style="font-size:14px;color:rgb(132, 128, 128)">{{item.name}}</span>
        </div>
        <div class="listNameTitle">
          <span style="color:#7a7a7a">事件名称:</span>
          <span style="font-size:14px;color:rgb(132, 128, 128)">{{item.eventName}}</span>
        </div>
        <div class="listNameTitle">
          <span style="color:#ccc">{{item.gmtCreate}}</span>
        </div>
      </div>
    </div>
    <AddComponent :ClickQuery="()=>{
              $router.push('EventControl')
            }" />
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-list-item>
            <div class="titleselect">
              <span>站点名称：</span>
            </div>
            <div class="select" @click="selectDataPicStation" id="classPicstation">
              <!-- <select name v-model="normal.value1">
                <option :value="item" v-for="(item,index) in options" :key="index">{{item}}</option>
              </select>-->
              <input type="text" placeholder="请选择站点" class="input" v-model="normal.value1" disabled />
              <img src="static/img/toBottom.png" alt="" srcset="">
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled v-model="startTime" placeholder="请选择开始时间" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectStartTime('end')">
              <input type="text" class="input" disabled v-model="lastTime" placeholder="请选择结束时间" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closed()">取消</div>
              <div
                class="right"
                :style="'background:'+$store.state.color"
                @click="confirmQuery()"
              >确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheetTime">
          <mu-sub-header>筛选时间</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="confirmTime"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import AddComponent from "@/components/add/index";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  name: "eventControlList",
  data() {
    return {
      back: "",
      list: [],
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      normal: {
        value1: "江碧1#水质监测站",
        value2: null
      },
      position: "",
      status: false,
      numberValue: null,
      token: "",
      open: false,
      startTime: "",
      lastTime: "",
      openTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      showTime: "",
      pageSize: 100000000
    };
  },
  components: {
    AddComponent
  },
  methods: {
    selectDataPicStation() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.open = true;
        }
      });
    },

    confirmTime(value) {
      var time = AppBace.filter(value);
      switch (this.showTime) {
        case "start":
          this.startTime = time;
          break;
          case "end":
          this.lastTime = time;
          break;
      }
      this.openTime = false;
    },
    close() {
      this.openTime = false;
    },
    closeBottomSheetTime() {},
    selectStartTime(e) {
      this.openTime = true;
      this.showTime = e;
    },
    selectLastTime(e) {
      this.openTime = true;
      this.showTime = e;
    },
    closed() {
      this.openTime = false;
      this.open = false;
      this.startTime = null;
      this.initData();
    },
    confirmQuery() {
      this.open = false;
      this.initData();
    },
    closeBottomSheet() {
      this.open = false;
    },
    clickListBtn(id) {
      this.$router.push({
        path: "/EventControlListDetails",
        query: { name: id }
      });
    },
    imgShowClose() {
      //   this.position = "position:absolute;top:400px;";
      this.open = true;
      // this.back = "background:#ccc;";
    },
    submitBtn() {
      this.status = false;
      this.back = "";
    },
    submitBtnBack() {
      this.normal.value1 = null;
      this.value3 = null;
      this.status = false;
      this.back = "";
    },
    async initData() {
      var url = "/job/event";
      var name1 = null;
      // var  sortField = false;
      this.token = sessionStorage.getItem("token");
      var name = this.lastTime;
      var pageNum = 1;
      var pageSize = this.pageSize;
      // var  sortOrder = false;
      var station = this.normal.value1;
      // this.normal.value1
      var time = this.startTime;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          name,
          time,
          station
        },
        this.token
      );
      // debugger
      if (res.data.data.total > this.pageSize) {
        this.pageSize = res.data.data.total;
      }
      this.list = res.data.data.rows;
      console.log(res);
      if (this.list.length === 0) {
        Toast("当前数据为空,请重新选择！");
      } else {
        // Toast("数据加载成功！");
      }
    }
  },
  mounted() {
    this.options = this.$store.state.menu;
    // this.normal.value1 = this.options[0];
    this.initData();
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.listNameTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 25px;
}
.containerListContent {
  /* width: 100%; */
  margin: 0 10px;
  padding: 10px 0;
  background: white;
  border-radius: 10px;
  margin-top: 70px;
}
.contentList {
  border-bottom: 1px solid rgb(243, 241, 241);
  height: 80px;
  /* display: flex; */
  /* align-items: center; */
  padding: 10px 0;
}
.positionImageSize {
  width: 100%;
  text-align: center;
  background: white;
  /* position: absolute;
  top: 60px; */
  padding: 0 30px;
  /* padding-bottom: 10px; */
}
.positionImageSize1 {
  width: 100%;
  text-align: center;
  background: white;
  position: absolute;
  top: 60px;
  box-shadow: 5px 5px 5px #888888;
  /* padding: 0; */
  /* padding-bottom: 10px; */
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.imgPosition {
  /* margin-bottom:-20px; */
  /* position: absolute;
    bottom: 0; */
  width: 100%;
  text-align: center;
  position: relative;
  top: -7px;
}
.imgPosition img {
  width: 60px;
}
.flexCenterMain {
  display: flex;
  align-content: center;
  height: 36px;
  border-bottom: 1px rgb(247, 245, 245) solid;
  margin-bottom: 10px;
}
.headerZINDEX {
  width: 100%;
  z-index: 999;
  position: absolute;
  top: 0;
}
.submitStyles {
  width: 100%;
  background: #4ed995;
  height: 50px;
  display: flex;
  color: white;
  font-size: 16px;
  padding: 0;
  /* align-content: center; */
  justify-content: center;
  align-items: center;
}
.submitStyles div {
  width: 50%;
}
#input {
  width: 100%;
  border: none;
  outline: none;
  /* border-bottom: 1px rgb(226, 225, 225) solid; */
  /* margin-bottom: 30px; */
}
.subDivBack {
  background: #e0f8ec;
  height: 50px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #4ed995;
  /* font-weight: 600; */
}
.icon img {
  width: 26px;
  height: 26px;
}
</style>



