      </div>
    </float-icons>
  </div>
</template>
 
<script>
import FloatIcons from "@/components/s-icons";
export default {
  components: {
    "float-icons": FloatIcons
  },
  props:["ClickQuery"],
  methods: {
    // 点击按钮
    handleIcons(router) {
    //   console.log("router", router);
      // this.$router.push(router);
      this.ClickQuery();

    },
    parant() {
      // alert(2);
    //   this.$parent.fatherMethod();
    }
  }
};
</script>
<style scoped>
.icons-warp {
  border-radius: 25px;
  opacity: 0.8;
}
.float-icon-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 50px;
  height: 50px;
  background: #d3d3d3;
  border-radius: 50%;
  opacity: 1;
}
img {
  width: 25px;
  height: 25px;
  margin-bottom: 3px;
}
span {
  font-size: 9px;
  color: rgb(252, 250, 250);
}
</style>


